.page-four {
    grid-column-start: 2;
    grid-column-end: 3;

    display: flex;
    align-items: center;
    justify-content: center;

}

.box-container {
    z-index: 2;
    display: flex;
    position: relative;

}

.box {
    position: relative;
    z-index: 2;
    padding: 40px 25px;
    width: 360px;
    height: 200px;
    box-shadow: 5px 5px 8px 4px rgba(56, 56, 56, 0.1);
    border-radius: 4px;
    background-color: #202a3c;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 40px;
}

.pf-text {
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.5px;
    line-height: 26px;
    text-align: start;
}

.pf-img-desc {
    display: flex;
    align-items: center;

}

.pf-img-desc img {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    margin-right: 10px;
}

.pf-desc-row {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
}

.pf-desc-row .name {

    color: #ffffff;
    font-family: "Open Sans";
    font-size: 10px;
    font-weight: 700;
    letter-spacing: 0.5px;
    line-height: 12px;
    margin-bottom: 5px;
}

.pf-desc-row .job {
    font-family: "Open Sans";
    font-size: 8px;
    font-weight: 400;
    letter-spacing: 0.57px;
    line-height: 10px;
}

.qutoes {
    position: absolute;
    top: 0;
    top: -35px;
    z-index: 0;
    left: -7px;
}