@import "./style/global.css";


.App {
  text-align: center;
  box-sizing: border-box;
}

.main-nav {
  position: fixed;
  z-index: 30;
  padding-left: 80px;
  padding-top: 35px;
  padding-bottom: 35px;
  display: flex;
  justify-content: space-between;
  width: 100vw;
  align-items: center;
  background-color: #0b1524c9;

}

.nav-links {
  display: flex;
  justify-content: space-between;
  color: white;
  margin-right: 12%;
}

.nav-link-text {
  margin-right: 50px;

}

.pointer {
  cursor: pointer;
}



.main {
  display: grid;
  grid-template-columns: 80px 1fr 80px;
  grid-template-rows: 1200px 600px 600px 600px 600px;
  background-color: #181f2b;
}


.page-five-bgc {
  grid-row-start: 5;
  background-color: #0b1524;
}

.page-five-bgc-two {
  grid-row-start: 5;
  grid-column-start: 3;
  background-color: #0b1524;
}