.page-one {
    background-color: #1b2330;
    grid-column-start: 1;
    grid-column-end: -1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
}

.mainImg {
    width: 720px;
    height: 534px;
}


.po-headline {
    color: #ffffff;
    font-family: Raleway;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    z-index: 2;

}

.po-desc {
    font-family: "Open Sans";
    font-size: 20px;
    font-weight: 400;
    color: #ffffff;
    line-height: 30px;
    z-index: 2;

}

.page-one button {
    box-shadow: 2px 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 28px;
    background-image: linear-gradient(152deg, #63e1d9 0%, #34a0cd 100%);
    width: 280px;
    height: 56px;
    z-index: 2;
    cursor: pointer;
    margin-top: 35px;
    color: white;
    outline: none;
    font-weight: bold;

}

.back {
    position: absolute;
    z-index: 1;
    bottom: 0;
    width: 100%;
    height: 41vh;

}

pre {
    margin: 0;
    padding: 0;
}