.page-two {
    grid-column-start: 2;
    grid-column-end: 3;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

}

.page-two-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 35vw;
    height: 32vh;

}

.page-two img {
    width: 76px;
    height: 72px;
}

.page-two .headline {
    color: #ffffff;
    font-family: Raleway;
    font-size: 20px;
    font-weight: 700;
}

.page-two .desc {
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
}

.page-two p {
    margin-bottom: 10px;
}

.page-two .file {
    width: 93px;
    height: 63px;
}

.page-two .collab {
    width: 89px;
    height: 71px;
}

.page-two .security {
    width: 72px;
    height: 92px;
}

.page-two .acsses {
    width: 89px;
    height: 81px;
}