.page-five {
    background-color: #0b1524;
    grid-column-start: 2;
    grid-column-end: 3;

    display: flex;
    flex-direction: column;
    justify-content: center;

}

.logo {
    align-self: flex-start;
    display: flex;
    margin-bottom: 30px;
}

.fylo-container {
    display: flex;
    flex-direction: column;
    width: 370px;
}

.location-desc img {
    margin-right: 25px;
    width: 13px;
    height: 18px;
}

.location-desc {
    display: flex;
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
}

.desc-column {
    display: flex;
    flex-direction: column;
}

.phone {
    margin-bottom: 25px;
}

.phoneImg {
    width: 18px;
    height: 18px;
    margin-right: 25px;

}

.emailImg {
    width: 20px;
    height: 16px;
    margin-right: 25px;


}

.contact-text {
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
}


.links-one-container {
    display: flex;
    flex-direction: column;

    color: #ffffff;
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 400;
    align-items: flex-start;
}

.link-one-text {
    margin-bottom: 20px !important;
}

.media-container {
    display: flex;
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 400;
    align-items: flex-start;
}

.media-container p {
    margin-right: 5px;

}

.media-container .img-container {
    border: 1px solid white;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-right: 20px;
    padding: 10px;
    cursor: pointer;
}





.flex {
    display: flex;
}

.j-sb {
    justify-content: space-between;
}