.page-three {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column-start: 2;
    grid-column-end: 3;

}

.page-three .text-container {
    margin-left: 9vw;
    height: 294px;
    display: flex;
    flex-direction: column;
    text-align: start;
}

.page-three .headline {
    color: #ffffff;
    font-family: Raleway;
    font-size: 50px;
    font-weight: 700;
    line-height: 50px;
}

.page-three .desc {
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 22px;
    font-weight: 400;
    line-height: 24px;
}

p {
    margin: 0;
    padding: 0;
}

.page-three .img-container img {

    width: 615px;
    height: 464px;
}

.arrow {
    color: #62e0d9;
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 400;
    text-decoration: underline;
    cursor: pointer;
    margin-top: 25px;
}

.arrow img {
    width: 12px;
    height: 12px;
    text-decoration: underline;
}